import { Mark } from '@ali/4ever-cangjie';

interface Strike extends Mark {
  type: 'strike';
};

const Strike = {
  TYPE: 'strike',

  create(): Strike {
    return Mark.create({ type: Strike.TYPE }) as Strike;
  },

  isStrike(maybeStrike: any): maybeStrike is Strike {
    return Mark.isMark(maybeStrike) && maybeStrike.type === Strike.TYPE;
  },
};

export default Strike;
